import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IMetadataFieldResponse } from './webhook-callback-configuration.interface';

@Injectable({
  providedIn: 'root'
})
export class RxWebhookCallbackConfigurationService {
  private metadataFieldData: Observable<IMetadataFieldResponse> = this.httpClient.get<IMetadataFieldResponse>(
    '/api/rx/application/webhook/processorsmetadata'
  );

  constructor(private httpClient: HttpClient) {}

  getMetadataFields(): Observable<IMetadataFieldResponse> {
    return this.metadataFieldData;
  }
}
